import { useParams, useNavigate, Link } from 'react-router-dom';
import { type Profile } from '../../lib/supabase';
import { useAuth } from '../../contexts/AuthContext';
import Navbar from '../../components/feature/Navbar';
import { getOptimizedImageUrl } from '../../lib/imageOptimization';
import { useProduct } from '../../hooks/useProducts';
import { useCreateConversation, useConversations } from '../../hooks/useConversations';

export default function ProductDetail() {
  const { id } = useParams();
  const { user } = useAuth();
  const navigate = useNavigate();

  const { data: product, isLoading: loading } = useProduct(id);
  const { conversations = [] } = useConversations();
  const createConversationMutation = useCreateConversation();

  const handleWhatsAppContact = () => {
    if (!product?.whatsapp_number) {
      alert('WhatsApp number not available');
      return;
    }

    const message = encodeURIComponent(
      `Hi! I'm interested in your product: ${product.name}`
    );
    const whatsappUrl = `https://wa.me/${product.whatsapp_number.replace(/[^0-9]/g, '')}?text=${message}`;
    window.open(whatsappUrl, '_blank');
  };

  const handleInAppMessage = async () => {
    if (!user) {
      navigate('/login');
      return;
    }

    if (!product) return;

    // Prevent messaging yourself
    if (user.id === product.seller_id) {
      alert("You cannot message yourself!");
      return;
    }

    // Check if conversation already exists for this product
    // A conversation exists if the current user is a participant (buyer or seller) AND the product matches
    const existing = conversations?.find(c =>
      c.product_id === product.id &&
      (c.buyer_id === user.id || c.seller_id === user.id)
    );

    if (existing) {
      navigate('/messages');
      return;
    }

    createConversationMutation.mutate({
      otherUserId: product.seller_id,
      productId: product.id
    }, {
      onSuccess: () => {
        navigate('/messages');
      },
      onError: (error: any) => {
        console.error('Error creating conversation:', error);
        alert(`Failed to start conversation: ${error.message || 'Unknown error'}`);
      }
    });
  };

  if (loading) {
    return (
      <div className="min-h-screen bg-gray-50 dark:bg-gray-950 pt-20 transition-colors duration-300">
        <Navbar />
        <div className="text-center py-24">
          <div className="inline-block w-12 h-12 border-4 border-gray-200 border-t-emerald-600 rounded-full animate-spin"></div>
          <p className="mt-4 text-gray-600 dark:text-gray-400 font-medium">Loading product details...</p>
        </div>
      </div>
    );
  }

  if (!product) {
    return (
      <div className="min-h-screen bg-gray-50 dark:bg-gray-950 pt-20 transition-colors duration-300">
        <Navbar />
        <div className="text-center py-24 max-w-md mx-auto">
          <div className="w-24 h-24 bg-red-50 dark:bg-red-900/20 rounded-full flex items-center justify-center mx-auto mb-6">
            <i className="ri-shopping-bag-line text-5xl text-red-300 dark:text-red-500"></i>
          </div>
          <h3 className="text-2xl font-bold text-gray-900 dark:text-white mb-2">Product not found</h3>
          <p className="text-gray-600 dark:text-gray-400 mb-8">The product you are looking for might have been sold or removed by the seller.</p>
          <Link to="/marketplace" className="inline-flex items-center px-6 py-3 bg-blue-600 text-white font-semibold rounded-lg hover:bg-blue-700 transition-colors">
            <i className="ri-arrow-left-line mr-2"></i>
            Back to marketplace
          </Link>
        </div>
      </div>
    );
  }

  const seller = product.seller as Profile;

  return (
    <div className="min-h-screen bg-gray-50 dark:bg-gray-950 pt-24 pb-32 transition-colors duration-300">
      <Navbar />

      <div className="max-w-6xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        <div className="mb-6">
          <Link
            to="/marketplace"
            className="inline-flex items-center text-blue-600 hover:text-blue-700 font-bold text-sm uppercase tracking-widest transition-colors cursor-pointer"
          >
            <i className="ri-arrow-left-line mr-2"></i>
            Back to Marketplace
          </Link>
        </div>

        <div className="bg-white dark:bg-gray-900 rounded-[2.5rem] shadow-xl shadow-gray-200/40 dark:shadow-none border border-gray-50 dark:border-gray-800 overflow-hidden">
          <div className="grid grid-cols-1 lg:grid-cols-2 gap-8 md:gap-12 p-6 md:p-12">
            <div className="aspect-[4/5] md:aspect-square bg-gray-50 dark:bg-gray-800 rounded-[2rem] overflow-hidden border border-gray-50 dark:border-gray-800 flex items-center justify-center relative group">
              {product.images && product.images.length > 0 ? (
                <img
                  src={getOptimizedImageUrl(product.images[0], 1000, 85)}
                  alt={product.name}
                  className="w-full h-full object-cover transition-transform duration-500 group-hover:scale-105"
                  loading="eager"
                  decoding="async"
                />
              ) : (
                <div className="w-full h-full flex items-center justify-center">
                  <i className="ri-image-line text-6xl text-gray-200 dark:text-gray-700"></i>
                </div>
              )}
              {product.is_active === false && (
                <div className="absolute inset-0 bg-black/60 backdrop-blur-sm flex items-center justify-center">
                  <span className="px-8 py-3 bg-red-600 text-white font-bold rounded-full text-lg uppercase tracking-widest shadow-xl">Sold Out</span>
                </div>
              )}
            </div>

            <div className="flex flex-col">
              <div className="flex items-center justify-between mb-8">
                <span className="px-4 py-1.5 bg-blue-600 text-white text-[10px] md:text-xs font-bold uppercase tracking-widest rounded-full shadow-lg shadow-blue-500/20">
                  {product.category}
                </span>
                <div className="flex items-center text-gray-400 text-xs md:text-sm font-bold uppercase tracking-widest">
                  <i className="ri-eye-line mr-2 text-blue-600"></i>
                  {product.views_count || 0} Views
                </div>
              </div>

              <h1 className="text-3xl md:text-5xl font-bold text-gray-900 dark:text-white mb-6 leading-tight tracking-tight">
                {product.name}
              </h1>

              <div className="mb-8 p-8 bg-gray-50 dark:bg-gray-800/50 rounded-[2rem] border border-gray-100 dark:border-gray-800">
                {product.price_type === 'fixed' ? (
                  <div className="flex flex-col">
                    <span className="text-[10px] font-bold text-gray-400 uppercase tracking-widest mb-2">Listing Price</span>
                    <span className="text-3xl md:text-5xl font-bold text-blue-600 tracking-tight">GHâ‚µ{product.price?.toLocaleString()}</span>
                  </div>
                ) : (
                  <div className="flex flex-col">
                    <span className="text-[10px] font-bold text-gray-400 uppercase tracking-widest mb-2">Pricing Terms</span>
                    <p className="text-xl md:text-2xl font-bold text-blue-600 uppercase tracking-widest">Contact for Quote</p>
                  </div>
                )}
              </div>

              <div className="mb-10">
                <h3 className="text-[10px] font-bold text-gray-400 uppercase tracking-widest mb-4 ml-1">Product Description</h3>
                <div className="prose prose-blue max-w-none dark:prose-invert">
                  <p className="text-gray-600 dark:text-gray-300 leading-relaxed text-base font-medium">
                    {product.description || 'No detailed description provided by the seller.'}
                  </p>
                </div>
              </div>

              <div className="mb-10 p-6 bg-gray-50 dark:bg-gray-800/50 rounded-2xl border border-gray-100 dark:border-gray-800 flex items-center justify-between">
                <div className="flex items-center space-x-4">
                  <div className="w-12 h-12 rounded-xl overflow-hidden shadow-sm border-2 border-white dark:border-gray-700">
                    {seller?.avatar_url ? (
                      <img
                        src={getOptimizedImageUrl(seller.avatar_url, 128, 80)}
                        alt={seller.full_name}
                        className="w-full h-full object-cover"
                      />
                    ) : (
                      <div className="w-full h-full bg-gray-900 dark:bg-gray-700 flex items-center justify-center">
                        <span className="text-white font-bold text-lg">
                          {seller?.full_name?.charAt(0).toUpperCase() || 'S'}
                        </span>
                      </div>
                    )}
                  </div>
                  <div>
                    <p className="text-[10px] font-bold text-gray-400 uppercase tracking-widest mb-0.5">Verified Seller</p>
                    <p className="font-bold text-gray-900 dark:text-white text-base tracking-tight">{seller?.full_name || 'Campus Student'}</p>
                  </div>
                </div>
                <div className="text-right">
                  <p className="text-[10px] font-bold text-gray-400 uppercase tracking-widest mb-0.5">Department</p>
                  <p className="text-xs font-bold text-gray-700 dark:text-gray-300">{seller?.department || 'University Member'}</p>
                </div>
              </div>

              <div className="fixed bottom-0 left-0 right-0 p-4 bg-white/95 dark:bg-gray-900/95 backdrop-blur-md border-t border-gray-100 dark:border-gray-800 z-50 lg:static lg:p-0 lg:bg-transparent lg:border-none shadow-[0_-10px_40px_-15px_rgba(0,0,0,0.1)] lg:shadow-none animate-slide-in-up">
                <div className="max-w-6xl mx-auto grid grid-cols-3 gap-2 lg:gap-3">
                  <button
                    onClick={handleWhatsAppContact}
                    className="w-full flex flex-col lg:flex-row items-center justify-center gap-1 lg:gap-2 px-2 lg:px-4 py-3 lg:py-4.5 bg-emerald-600 text-white font-bold rounded-xl lg:rounded-2xl hover:bg-emerald-700 shadow-lg shadow-emerald-500/20 transition-all cursor-pointer active:scale-95 text-[10px] lg:text-xs uppercase tracking-widest"
                    disabled={!product?.whatsapp_number}
                  >
                    <i className="ri-whatsapp-line text-lg lg:text-xl"></i>
                    <span className="hidden leading-none sm:inline">WhatsApp</span>
                  </button>

                  <a
                    href={`tel:${seller?.phone}`}
                    className={`w-full flex flex-col lg:flex-row items-center justify-center gap-1 lg:gap-2 px-2 lg:px-4 py-3 lg:py-4.5 bg-blue-600 text-white font-bold rounded-xl lg:rounded-2xl hover:bg-blue-700 shadow-lg shadow-blue-500/20 transition-all cursor-pointer active:scale-95 text-[10px] lg:text-xs uppercase tracking-widest ${!seller?.phone ? 'opacity-50 pointer-events-none' : ''}`}
                  >
                    <i className="ri-phone-line text-lg lg:text-xl"></i>
                    <span className="hidden leading-none sm:inline">Call</span>
                  </a>

                  <button
                    onClick={handleInAppMessage}
                    className="w-full flex flex-col lg:flex-row items-center justify-center gap-1 lg:gap-2 px-2 lg:px-4 py-3 lg:py-4.5 bg-gray-900 dark:bg-white text-white dark:text-gray-900 font-bold rounded-xl lg:rounded-2xl hover:bg-black dark:hover:bg-gray-100 shadow-lg shadow-gray-900/20 transition-all cursor-pointer active:scale-95 text-[10px] lg:text-xs uppercase tracking-widest"
                    disabled={createConversationMutation.isPending}
                  >
                    <i className="ri-message-3-line text-lg lg:text-xl"></i>
                    <span className="hidden leading-none sm:inline">{createConversationMutation.isPending ? '...' : 'Chat'}</span>
                  </button>
                </div>
              </div>


            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
